"use client";

import { complianceValidator, type ComplianceReport } from "./adr-validator";

export interface Task44ComplianceReport {
  taskId: string;
  taskTitle: string;
  timestamp: string;
  implementationPhase: "completed";
  subtasks: {
    "44.1": ComplianceStatus;
    "44.2": ComplianceStatus;
    "44.3": ComplianceStatus;
    "44.4": ComplianceStatus;
    "44.5": ComplianceStatus;
  };
  adrCompliance: {
    "ADR-002": ADRComplianceDetail;
    "ADR-008": ADRComplianceDetail;
  };
  performanceMetrics: PerformanceComplianceDetail;
  architecturalCompliance: ArchitecturalComplianceDetail;
  expertCommitteeReadiness: ExpertCommitteeReadinessDetail;
  recommendations: string[];
  overallScore: number;
  status: "ready-for-review" | "needs-improvements" | "compliant";
}

export interface ComplianceStatus {
  completed: boolean;
  adrsFollowed: string[];
  componentsCreated: string[];
  performanceOptimized: boolean;
  safetyProtocolsFollowed: boolean;
  score: number;
  issues: string[];
}

export interface ADRComplianceDetail {
  title: string;
  compliant: boolean;
  score: number;
  checkedItems: Array<{
    requirement: string;
    status: "compliant" | "non-compliant" | "partial";
    evidence: string;
    location: string;
  }>;
  violations: string[];
  recommendations: string[];
}

export interface PerformanceComplianceDetail {
  optimizationTechniques: Array<{
    technique: string;
    implemented: boolean;
    location: string;
    impact: "high" | "medium" | "low";
  }>;
  renderPerformance: {
    averageRenderTime: number;
    memoizationUsage: string[];
    virtualizedComponents: string[];
    batchedUpdates: string[];
  };
  memoryManagement: {
    cacheImplementation: string[];
    memoryLeakPrevention: string[];
    cleanupStrategies: string[];
  };
  loadingOptimizations: {
    lazyLoading: string[];
    codesplitting: string[];
    bundleOptimization: string[];
  };
}

export interface ArchitecturalComplianceDetail {
  layerSeparation: {
    uiComponents: string[];
    businessLogic: string[];
    dataLayer: string[];
    apiIntegration: string[];
  };
  dependencyManagement: {
    cleanImports: boolean;
    circularDependencies: string[];
    unusedImports: string[];
  };
  componentArchitecture: {
    singleResponsibility: boolean;
    reusability: number; // 0-100 score
    testability: number; // 0-100 score
    maintainability: number; // 0-100 score
  };
}

export interface ExpertCommitteeReadinessDetail {
  documentationComplete: boolean;
  codeReviewReady: boolean;
  testCoverage: number; // percentage
  performanceBenchmarks: {
    renderTime: number;
    memoryUsage: number;
    bundleSize: number;
    cacheEfficiency: number;
  };
  safetyValidation: {
    inputSanitization: boolean;
    errorHandling: boolean;
    boundsChecking: boolean;
    accessControl: boolean;
  };
  deploymentReadiness: {
    environmentConfig: boolean;
    dependencyAudit: boolean;
    securityScan: boolean;
    performanceProfile: boolean;
  };
}

